import Vue from "vue";
import VueRouter from "vue-router";
import Login from "../views/Login.vue";
import Home from "../views/Home.vue";
import SeccionPluviometros from "../views/SeccionPluviometros.vue";
import SeccionPluviometro from "../views/SeccionPluviometro.vue";
import SeccionFreatimetros from "../views/SeccionFreatimetros.vue";
import SeccionFreatimetro from "../views/SeccionFreatimetro.vue";
import SeccionVerMapa from "../views/SeccionVerMapa.vue";
import Productores from "../views/Admin/Productores.vue";
import Establecimientos from "../views/Admin/Establecimientos.vue";
import SeccionVeris from "../views/SeccionVeris.vue";
import SeccionVeri from "../views/SeccionVeri.vue";
import SeccionAguas from "../views/SeccionAguas.vue";
import SeccionRegistrosAgua from "../views/SeccionRegistrosAgua.vue";
import SeccionSuelos from "../views/SeccionSuelos.vue";
import SeccionRegistrosSuelo from "../views/SeccionRegistrosSuelo.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/index.html",
    name: "Home1",
    component: Home,
  },
  {
    path: "/login",
    name: "Login",
    component: Login,
  },
  {
    path: "/pluviometros",
    name: "pluviometros",
    component: SeccionPluviometros,
  },
  {
    path: "/pluviometro/:Id?",
    name: "Pluviometro",
    component: SeccionPluviometro,
  },
  {
    path: "/veris",
    name: "Veris",
    component: SeccionVeris,
  },
  {
    path: "/veris/:Id?",
    name: "Veri",
    component: SeccionVeri,
  },
  {
    path: "/establecimientos/:Id?",
    name: "Establecimientos",
    component: Establecimientos,
  },
  {
    path: "/freatimetros/",
    name: "Freatimetros",
    component: SeccionFreatimetros,
  },
  {
    path: "/freatimetro/:Id?",
    name: "Freatimetro",
    component: SeccionFreatimetro,
  },
  {
    path: "/vermapa",
    name: "VerMapa",
    component: SeccionVerMapa,
  },
  {
    path: "/productores",
    name: "Productores",
    component: Productores,
  },
  {
    path: "/aguas",
    name: "Aguas",
    component: SeccionAguas,
  },
  {
    path: "/aguas/:Id?",
    name: "RegistrosAgua",
    component: SeccionRegistrosAgua,
  },
  {
    path: "/suelos",
    name: "Suelos",
    component: SeccionSuelos,
  },
  {
    path: "/suelos/:Id?",
    name: "RegistrosSuelo",
    component: SeccionRegistrosSuelo,
  },
];
  
const router = new VueRouter({
  mode: "history",
  //mode : 'hash',
  base: process.env.BASE_URL,
  //base: "/AgroIdeas-MaizPlus/",
  routes,

  // history: true,
  // hashbang: false,
  // // root: "/your app directory",
  // base: "",
});

export default router;
