//#region "PWA"
var swRegistration;
function ChequearServiceWorkerPorPWA() {
  try {
      // var _SessionID = getCookie("SessionID");
      //var _UpdatearPUSHs = (typeof swRegistration === 'undefined');
      //if (_SessionID != null && _SessionID != "") {
      //    _UpdatearPUSHs = true;
      //}
      var _HaySwInstalado = false;
      if (navigator.serviceWorker != undefined) {
          _HaySwInstalado = navigator.serviceWorker.controller != null;
      }
      var _UrlSW = process.env.NODE_ENV === "production" ? '/agroideas-maizplus/sw.js' : './sw.js';
      if ('serviceWorker' in navigator) {
          navigator.serviceWorker.register(_UrlSW)
              .then(function (registration) {
                  console.warn('Service worker registrado OK!');
                  registration.addEventListener('updatefound', function () {
                      var installingWorker = registration.installing;
                      console.log('Un nuevo SW está instalándose.', installingWorker);
                      if (_HaySwInstalado) {
                          AccionSwActualizado();
                      }
                  });
                  // _UpdatearPUSHs = (typeof swRegistration === 'undefined');//da siempre true
                  // //Para los PUSHs
                  // if (_UpdatearPUSHs) {
                  //     registration.update();
                  //     swRegistration = registration;
                  //     CheckearSuscripcionUsuario();//Es asíncrona
                  // }
              })
              .catch(function (error) {
                  console.log('Service worker registration failed:', error);
              });
      } else {
          console.log('Service workers are not supported.');
      }
  } catch (e) { console.log(e); }
}
