<template>
  <div class="w100 Contenido">
    <div class="TituloSeccion">
      <font-awesome-icon  icon="ruler-vertical" size="4x" :style="{ color: 'black'}"  class="IconoTituloSeccion fLeft"/>
      <div class="fLeft">Freatímetros</div>
      <div class="clear"></div>
    </div>
    
    <div class="DivInfoData" ref="DivInfoData">
      Info recuperada de fecha: <span ref="FechaData"></span>
      
      <div class="fRight" v-on:click.stop="ObtenerDatos()">
        <font-awesome-icon  icon="sync-alt" size="4x" :style="{ color: 'white'}"  class="IconoActualizar fLeft"/>
        <!-- Actualizar -->
        <div class="clear"></div>
      </div>
      
      <div class="clear"></div>
    </div>

    <div class="w100 aliCenter">
      <table class="tableFixHead">
        <thead>
          <tr>
            <th>Productor</th>
            <th>Establ.</th>
            <th>Fecha</th>
            <th>Localidad</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="p in Freatimetros" :key="p._Id" v-on:click="VerFreatimetro(p._Id)">
            <td>{{ p._NombreProductor }}</td>
            <td>{{ p._NombreEstablecimiento }}</td>
            <td>{{ p._FechaAlta }}</td>
            <td>{{ p._Localidad }}</td>
          </tr>
        </tbody>
        <tbody></tbody>
      </table>
    </div>
    
    <br><br>
    <router-link class="BotonVerMapa dIB" to="/vermapa?t=2">Ver Mapa</router-link>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Freatimetros",
  data() {
    return {
      Freatimetros: null,
      _UserOk: false,
      Error: null,
    };
  },
  mounted() {
    this._UserOk = this.$parent.Datos.ChequearSesionUsuario(this.$router);
    if (this._UserOk){
      this.ObtenerDatos();
    }
  },
  methods: {
    ObtenerDatos() {
      var _This = this;
      var _User = _This.$parent.Datos._User;
      if (_User == null) {
        _This.$router.push("/login");
      } else {
        axios
          .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/Freatimetros", {
            IdUsuario: _User._ID
          })
          .then(function (response) {
            //console.log(response.data);
            _This.Freatimetros = response.data;
          })
          .catch(function (error) {
            _This.Error = error;
          })
          .then(function () {});
      }
    },
    VerFreatimetro(id) {
      this.$router.push("/freatimetro/" + id);
    },
  },
  components: {
  },
};
</script>

<style lang="scss" scoped>
@import "../Css/Componentes.scss";

.button {
  background-color: #4caf50; /* Green */
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  transition-duration: 0.4s;
  cursor: pointer;
  margin: 30px 0;
}
.Verde {
  background-color: white;
  color: black;
  border: 2px solid #4caf50;
}

.Verde:hover {
  background-color: #4caf50;
  color: white;
}

.Azul {
  background-color: white;
  color: black;
  border: 2px solid #008cba;
}

.Azul:hover {
  background-color: #008cba;
  color: white;
}

.Gris {
  background-color: white;
  color: black;
  border: 2px solid #e7e7e7;
}

.Gris:hover {
  background-color: #e7e7e7;
}

.Negro {
  background-color: white;
  color: black;
  border: 2px solid #555555;
}

.Negro:hover {
  background-color: #555555;
  color: white;
}

</style>
