<template>
<div class="w100">
  <!-- <transition name="modal-fade"> -->
    <div class="modal-backdrop" v-on:click="ClickOutside()">
    </div>
    <div class="modal" role="dialog" >
      <header class="modal-header" id="modalTitle" >
        <slot name="header">
          Nuevo Productor
        </slot>
      </header>
      <section class="modal-body mT10" id="modalDescription" >
          
        <div class="face face-front oHidden">
          <div class="content aliCenter">
            <!-- <div class="TituloLogin B">Login</div> -->
            <div class="">
              <div class="field-wrapper">
                <input v-model="Nombre" type="text" placeholder="Nombre" />
                <label>Nombre</label>
              </div>
              <div class="field-wrapper">
                <input v-model="Apellido" type="text" placeholder="Apellido" />
                <label>Apellido</label>
              </div>
              <div class="field-wrapper">
                <input v-model="Localidad" type="text" placeholder="Localidad" />
                <label>Localidad</label>
              </div>
              <div class="field-wrapper">
                <input v-model="Telefono" type="text" placeholder="Telefono" />
                <label>Telefono</label>
              </div>
            </div>
          </div>
        </div>
      </section>
      <footer class="modal-footer">
          <button type="button" class="Boton BotonCancelar" @click="Cerrar" >Cancelar</button>
          <button type="button" class="Boton BotonAceptar" @click="AgregarProductor()" >Aceptar</button>
      </footer>
    </div>
  <!-- </transition> -->
  
</div>
</template>
<script>
  import axios from "axios";
  export default {
    name: 'ModalNuevoProductor',
    data() {
      return {
        Nombre: "",
        Apellido: "",
        Localidad: "",
        Telefono: ""
      };
    },
    mounted() {
        // var _Body = "<u>Nick:</u> asdasdasdsd";
        // var _This = this;
        // setTimeout(() => {
        //     _This.Notificacion("asdada", _Body, "success", 1);
        // }, 2000);
    },
    methods: {
      Cerrar() {
        this.$emit('Cerrar');
      },
      ClickOutside(){
        this.$emit('Cerrar');
      },
      async AgregarProductor() {
        try {
          var _Nombre = this.Nombre.trim();
          var _Apellido = this.Apellido.trim();
          var _Localidad = this.Localidad.trim();
          var _Telefono = this.Telefono.trim();

          if (this.Validaciones(_Nombre, _Apellido, _Localidad, _Telefono)){
            if (await this.SolicitoAltaProductor(_Nombre, _Apellido, _Localidad, _Telefono)){
              this.LimpiarDatos();
              this.$parent.CerrarModal();
              this.$parent.ObtenerDatos();
            }
          }
        } catch (error) {
          console.error(error);
        }
      },
      async SolicitoAltaProductor(_Nombre, _Apellido, _Localidad, _Telefono){
        var _Retorno = false;
        try {
          var _This = this;
          var _User = _This.$parent.$parent.Datos._User;
          await axios
              .post(_This.$parent.$parent.Datos.UrlPosts() + "/AgroIdeas/AgregarProductor", {
                IdUsuario: _User._ID,
                InfoProductor: {
                  Nombre: _Nombre,
                  Apellido: _Apellido,
                  Localidad: _Localidad,
                  Telefono: _Telefono
                }
              })
              .then(function (response) {
                //_This.Productores = response.data;
                if (response.data._Nick != undefined){
                    var _Body = "<br/><u>Nick:</u> " + response.data._Nick;
                    _Body += "<br/><u>Pass:</u> 123456";
                    _This.Notificacion(response.data._Mensaje, _Body, "success", 20);
                    _Retorno = true;
                }else{
                    _This.Notificacion("El usuario ya existe!", "", "error", 3);
                }
              })
              .catch(function (error) {
                console.log(error);
              })
              .then(function () {console.log("---");});
        } catch (error) {
          console.error(error);
        }
        return _Retorno;
      },
      Validaciones(_Nombre, _Apellido, _Localidad, _Telefono){
        var _Retorno = true;
        try {
            if (_Nombre.length < 2){
              this.$notify({
                group: 'Notificaciones',
                title: 'Nombre inválido!',//Puede ser HTML
                type: 'error',
                speed: 500,
                duration: 3000,
                // text: 'Hello user! This is a notification!'//Puede ser HTML
              });
              _Retorno = false;
            }
            if (_Apellido.length < 2){
              this.$notify({
                group: 'Notificaciones',
                title: 'Apellido inválido!',//Puede ser HTML
                type: 'error',
                speed: 500,
                duration: 3000,
                // text: 'Hello user! This is a notification!'//Puede ser HTML
              });
              _Retorno = false;
            }
            if (_Localidad.length < 2){
              this.$notify({
                group: 'Notificaciones',
                title: 'Localidad inválida!',//Puede ser HTML
                type: 'error',
                speed: 500,
                duration: 3000,
                // text: 'Hello user! This is a notification!'//Puede ser HTML
              });
              _Retorno = false;
            }
            if (_Telefono.length < 2){
              this.$notify({
                group: 'Notificaciones',
                title: 'Telefono inválido!',//Puede ser HTML
                type: 'error',
                speed: 500,
                duration: 3000,
                // text: 'Hello user! This is a notification!'//Puede ser HTML
              });
              _Retorno = false;
            }
        } catch (error) {
          console.error(error);
        }
        return _Retorno;
      },
      Notificacion(_Titulo, _Body, _Tipo, _Segundos){
        this.$notify({
          group: 'Notificaciones',
          title: _Titulo,//Puede ser HTML
          type: _Tipo,
          speed: 500,
          duration: _Segundos * 1000,
          text: _Body//Puede ser HTML
        });
      },
      LimpiarDatos(){
        this.Nombre = "";
        this.Apellido = "";
        this.Localidad = "";
        this.Telefono = "";
      }
    },
  };
</script>
<style lang="scss" scoped>
    $AgroIdeas: #005625;
    $text-dark: #444;
    $navy-blue: #999;
    
    .face {
        width: 100%;
        padding: 0px 10px;
        box-sizing: border-box;

        .content {
            color: $text-dark;
            h2 {
            font-size: 1.2em;
            color: $AgroIdeas;
            }
            .field-wrapper {
            margin-top: 20px;
            position: relative;

            label {
                position: absolute;
                pointer-events: none;
                font-size: 0.85em;
                top: 40%;
                left: 0;
                transform: translateY(-5px);
                transition: all ease-in 0.25s;
                color: lighten($text-dark, 20%);
            }
            input[type="text"],
            input[type="password"],
            input[type="submit"],
            textarea {
                -webkit-appearance: none;
                appearance: none;
                &:focus {
                outline: none;
                }
            }
            input[type="text"],
            input[type="password"],
            textarea {
                width: 100%;
                border: none;
                background: transparent;
                line-height: 2em;
                border-bottom: 1px solid $AgroIdeas;
                color: $text-dark;

                &::-webkit-input-placeholder {
                opacity: 0;
                }
                &::-moz-placeholder {
                opacity: 0;
                }
                &:-ms-input-placeholder {
                opacity: 0;
                }
                &:-moz-placeholder {
                opacity: 0;
                }
                &:focus,
                &:not(:placeholder-shown) {
                + label {
                    top: -35%;
                    color: $navy-blue;
                }
                }
            }
            textarea {
                resize: none;
                line-height: 1em;
                &:focus,
                &:not(:placeholder-shown) {
                + label {
                    top: -25%;
                }
                }
            }
            }
        }
    }


    .modal-fade-enter, .modal-fade-leave-active {
        opacity: 0;
    }
    .modal-fade-enter-active, .modal-fade-leave-active {
        transition: opacity .2s ease
    }
    .modal-backdrop {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgba(0, 0, 0, 0.3);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 11;
    }
    .modal {
        background: #FFFFFF;
        box-shadow: 0px 0px 10px #222;
        overflow-x: auto;
        display: flex;
        flex-direction: column;
        width: 95%;
        max-width: 500px;
        
        position: fixed;
        justify-content: center;
        z-index: 11;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
    .modal-header,
    .modal-footer {
        padding: 15px;
        display: flex;
        // border-top: 1px solid #eeeeee;
        justify-content: space-between;
    }
    .modal-header {
        border-bottom: 1px solid #eeeeee;
        font-weight: bold;
        background: rgba(0,0,0,0.8);
        color: #CCC;
    }
    .modal-footer {
        margin-top:20px;
    }
    .Boton{
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        cursor: pointer;
        width: 100px;
        line-height: 2em;
        color: #fff;
        border: 1px solid #000;
        border-radius: 3px;
        padding: 5px;
        &:hover {
            opacity: 0.9;
        }
        &:active {
            transform: scale(0.96);
        }
    }
    .BotonCancelar {  
        background: #999;
    }
    .BotonAceptar {  
        background: $AgroIdeas;
    }
</style>