<template>
<div class="w100">
  <!-- <transition name="modal-fade"> -->
    <div class="modal-backdrop" v-on:click="ClickOutside()">
    </div>
    <div class="modal" role="dialog" >
      <header class="modal-header" id="modalTitle" >
        <slot name="header">
          <div ref="Titulo"></div>
        </slot>
      </header>
      <section class="modal-body mT10" id="modalDescription" >
          
        <div class="face face-front oHidden">
          <div class="content aliCenter">
            <div class="Aclaracion">(Dejar campos vacíos si se quiere utilizar las mismas coordenadas del establecimiento)</div>
            <div class="">
              <div class="field-wrapper">
                <input v-model="Info._Latitud" type="text" placeholder="Latitud" />
                <label>Latitud (ej: -31.0302)</label>
              </div>
              <div class="field-wrapper">
                <input v-model="Info._Longitud" type="text" placeholder="Longitud" />
                <label>Longitud (ej: -31.0302)</label>
              </div>
            </div>
          </div>
        </div>
      </section>
      <footer class="modal-footer">
          <button type="button" class="Boton BotonCancelar" @click="Cerrar" >Cancelar</button>
          <button type="button" class="Boton BotonAceptar" @click="Aceptar()" >Aceptar</button>
      </footer>
    </div>
  <!-- </transition> -->
  
</div>
</template>
<script>
  import axios from "axios";
  export default {
    name: 'EdicionLatLon',
    // props: ['Id', 'Caso', 'DataEjeY'],
    data() {
      return {
        Info: {
          _Id: 0,
          _LatitudOrig: "",
          _LongitudOrig: "",
          _Latitud: "",
          _Longitud: ""
        },
        _Caso: 1// 1 Pluv, 2 Freat
      };
    },
    methods: {
      Cerrar() {
        this.$emit('Cerrar');
      },
      ClickOutside(){
        this.LimpiarDatos();
        this.$emit('Cerrar');
      },
      Editar(_Info, _Id, _Caso){
        if (_Caso == 1){
            this.$refs.Titulo.innerHTML = "Editar coordenadas Pluviómetro...";
        }else{
            this.$refs.Titulo.innerHTML = "Editar coordenadas Freatímetro...";
        }
        this._Caso = _Caso;
        this.Info._Id = _Id;
        this.Info._LatitudOrig = _Info._Latitud;
        this.Info._LongitudOrig = _Info._Longitud;
        this.Info._Latitud = _Info._Latitud;
        this.Info._Longitud = _Info._Longitud;
      },
      async Aceptar() {
        try {
            var _RetornoOk = false;
            this.Info._Latitud = this.Info._Latitud.toString().trim().ObtenerNumero();
            this.Info._Longitud = this.Info._Longitud.toString().trim().ObtenerNumero();
            if(!(this.Info._Latitud == this.Info._LatitudOrig && this.Info._Longitud == this.Info._LongitudOrig)){ 
                if (this.Validaciones()){
                    switch (this._Caso){
                    case 1:
                        if (await this.SolicitoEdicion("Pluviometro")){//PLUVIOMETRO
                            _RetornoOk = true;
                        }
                        break;
                    default:
                        if (await this.SolicitoEdicion("Freatimetro")){//FREATIMETRO
                            _RetornoOk = true;
                        }
                        break;
                    }
                }
            }else{
                this.$parent.$parent.Notificacion("", "No se detectaron cambios!", "alert", 3);
            }

            if(_RetornoOk){
                this.$parent.SetearNuevasCoordenadas(this.Info._Latitud, this.Info._Longitud);
            }
            this.LimpiarDatos();
            this.$parent.CerrarModalEdicion();
        } catch (error) {
          console.error(error);
        }
      },
      async SolicitoEdicion(_Caso){
        var _Retorno = false;
        try {
          var _This = this;
          var _User = _This.$parent.$parent.Datos._User;
          await axios
              .post(_This.$parent.$parent.Datos.UrlPosts() + "/AgroIdeas/Edicion" + _Caso, {
                IdUsuario: _User._ID,
                Info: _This.Info
              })
              .then(function (response) {
                //console.log(response.data);
                if (response.data == ""){
                  _Retorno = true;
                  _This.$parent.$parent.Notificacion(_Caso + " actualizado!", "", "success", 3);
                }else{
                  _This.$parent.$parent.Notificacion(response.data, "", "error", 3);
                }
              })
              .catch(function (error) {
                console.log(error);
                _This.$parent.$parent.Notificacion("Error!", "", "error", 3);
              })
              .then(function () {});
        } catch (error) {
          console.error(error);
        }
        return _Retorno;
      },
      Validaciones(){
        var _Retorno = true;
        try {
            if (this.Info._Latitud.length < 2){
              this.$notify({
                group: 'Notificaciones',
                title: 'Latitud inválida!',//Puede ser HTML
                type: 'error',
                speed: 500,
                duration: 3000,
                // text: 'Hello user! This is a notification!'//Puede ser HTML
              });
              _Retorno = false;
            }
            if (this.Info._Longitud.length < 2){
              this.$notify({
                group: 'Notificaciones',
                title: 'Longitud inválido!',//Puede ser HTML
                type: 'error',
                speed: 500,
                duration: 3000,
                // text: 'Hello user! This is a notification!'//Puede ser HTML
              });
              _Retorno = false;
            }
        } catch (error) {
          console.error(error);
        }
        return _Retorno;
      },
      LimpiarDatos(){
        this.Info._Latitud = "";
        this.Info._Longitud = "";
      }
    },
  };
</script>
<style lang="scss" scoped>
    $AgroIdeas: #005625;
    $text-dark: #444;
    $navy-blue: #999;
    
    .Aclaracion{
        font-size:13px;
    }
    .face {
        width: 100%;
        padding: 0px 10px;
        box-sizing: border-box;

        .content {
            color: $text-dark;
            h2 {
            font-size: 1.2em;
            color: $AgroIdeas;
            }
            .field-wrapper {
            margin-top: 20px;
            position: relative;

            label {
                position: absolute;
                pointer-events: none;
                font-size: 0.85em;
                top: 40%;
                left: 0;
                transform: translateY(-5px);
                transition: all ease-in 0.25s;
                color: lighten($text-dark, 20%);
            }
            input[type="text"],
            input[type="password"],
            input[type="submit"],
            textarea {
                -webkit-appearance: none;
                appearance: none;
                &:focus {
                outline: none;
                }
            }
            input[type="text"],
            input[type="password"],
            textarea {
                width: 100%;
                border: none;
                background: transparent;
                line-height: 2em;
                border-bottom: 1px solid $AgroIdeas;
                color: $text-dark;

                &::-webkit-input-placeholder {
                opacity: 0;
                }
                &::-moz-placeholder {
                opacity: 0;
                }
                &:-ms-input-placeholder {
                opacity: 0;
                }
                &:-moz-placeholder {
                opacity: 0;
                }
                &:focus,
                &:not(:placeholder-shown) {
                + label {
                    top: -35%;
                    color: $navy-blue;
                }
                }
            }
            textarea {
                resize: none;
                line-height: 1em;
                &:focus,
                &:not(:placeholder-shown) {
                + label {
                    top: -25%;
                }
                }
            }
            }
        }
    }


    .modal-fade-enter, .modal-fade-leave-active {
        opacity: 0;
    }
    .modal-fade-enter-active, .modal-fade-leave-active {
        transition: opacity .2s ease
    }
    .modal-backdrop {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgba(0, 0, 0, 0.3);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 11;
    }
    .modal {
        background: #FFFFFF;
        box-shadow: 0px 0px 10px #222;
        overflow-x: auto;
        display: flex;
        flex-direction: column;
        width: 95%;
        max-width: 500px;
        
        position: fixed;
        justify-content: center;
        z-index: 11;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
    .modal-header,
    .modal-footer {
        padding: 15px;
        display: flex;
        // border-top: 1px solid #eeeeee;
        justify-content: space-between;
    }
    .modal-header {
        border-bottom: 1px solid #eeeeee;
        font-weight: bold;
        background: rgba(0,0,0,0.8);
        color: #CCC;
    }
    .modal-footer {
        margin-top:20px;
    }
    .Boton{
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        cursor: pointer;
        width: 100px;
        line-height: 2em;
        color: #fff;
        border: 1px solid #000;
        border-radius: 3px;
        padding: 5px;
        &:hover {
            opacity: 0.9;
        }
        &:active {
            transform: scale(0.96);
        }
    }
    .BotonCancelar {  
        background: #999;
    }
    .BotonAceptar {  
        background: $AgroIdeas;
    }
</style>