<template>
  <div class="ContenidoComponente">
    <!-- <apexcharts
      width="100%"
      type="bar"
      :options="this.DataEjeX"
      :series="this.DataEjeY"
    ></apexcharts> -->
    <apexcharts
      width="100%"
      type="bar"
      :options="this.chartOptions"
      :series="this.series"
      class="EstilosGrafico"
    ></apexcharts>

    <br />
  </div>
</template>

<script>
import VueApexCharts from "vue-apexcharts";

export default {
  name: "Grafico",
  components: {
    apexcharts: VueApexCharts,
  },
  watch: {
    DataEjeY: {
      // the callback will be called immediately after the start of the observation
      immediate: true, 
      deep: true,
      handler (val, oldVal) {
        // do your stuff
        this.ActualizarGrafico();
      },
    }
  },
  data: function() {
    //console.log(this.$props);
    return {
      //Grafico
      chartOptions: {
        chart: {
          id: "basic-bar",
        },
        xaxis: {
          categories: [],
          labels: {
            show: true,
            rotate: -45,
            rotateAlways: true,
          },
        },
        yaxis: {
          title: {
            text: "this.DataEjeY",
          },
        },
        noData: {
          text: "Aún no hay datos...",
        },
      },
      series: [
        {
          name: this.TituloEjeX,
          data: [],//this.$props.DataEjeY, //.UltimosCincoItems(),
        },
      ],
    };
  },
  props: ['TituloEjeY', 'DataEjeX', 'DataEjeY'],
  mounted() {
    this.ActualizarGrafico();
    //console.log("asda 1");
    // this.SepararClaveValor();
    //console.log(this.$props.DataEjeX);
  },
  methods: {
    ActualizarGrafico(){
      this.chartOptions = {
        xaxis: {
          categories: this.DataEjeX,
        },
      };

      this.series = [
        {
          data: this.DataEjeY,
        },
      ];
    },
  },
};
</script>


<style lang="scss" scoped>
  .ContenidoComponente{
    display:flex;
    align-items: center;
    justify-content: center;
  }
  .EstilosGrafico{
    width:calc(100% - 20px);
    max-width:500px;
  }
</style>