module.exports.MiIP = async () => {
  var _Retorno = "";
  await axios
            .get("https://api.ipify.org/?format=json")
            .then(function (r) {
                //console.log(r);
                _Retorno = r.data.ip;
                //_This.$parent.Notificacion("Registro guardado!", "También ha quedado correctamente sincronizado con el servidor.", "success", 8);
            })
            .catch(function (error) {
                
            });
  return _Retorno;
}

