module.exports.ProcesoCargasPendientes = async(_AppVue) => {
    try {
      if (_AppVue.Datos.AppOnline()){
        ActualizacionRegistrosPluviometros(_AppVue);
        ActualizacionRegistrosFreatimetros(_AppVue);
      }
    
      //Vuelvo a llamar la funcion
      setTimeout(() => {
        this.ProcesoCargasPendientes(_AppVue)
      }, 2000);
      
    } catch (e) {
      console.log(e);
    }
}

//#region "Funciones PLUVIOMETROS"
var _KeyDataCacheadaPluviometros = "InfoPluviometro";
module.exports.KeyDataCacheadaPluviometros = (_IdPluviometro) => {
  return _KeyDataCacheadaPluviometros + _IdPluviometro;
}
module.exports.GuardarDataEnCachePluviometro = (_InfoAGuardar) => {
  var _Data = {
    _Fecha: new Clases.Fecha(new Date()),
    _Key: "InfoPluviometro" + _InfoAGuardar._IdPluviometro,
    _Json: _InfoAGuardar
  };
  Clases.GuardarJsonEnStorage(this.KeyDataCacheadaPluviometros(_InfoAGuardar._IdPluviometro), _Data);
}

module.exports._KeyRegistrosPendientesGeneralPluviometros = "RegistrosPendientes_Pluviometros_";
ActualizacionRegistrosPluviometros = async(_AppVue) => {
    try {
        //console.log(_AppVue.$children);
        // console.log(Vue.$options.components["SeccionPluviometro"]);
        var _ArchivosSincronizados = 0;
        var _Pendientes = Clases.RecuperarJsonsDeStorage(this._KeyRegistrosPendientesGeneralPluviometros);
        for (let index = 0; index < _Pendientes.length; index++){
            var _Registros = _Pendientes[index];
            var _RegistrosAux = []; 
            var _HuboActualizaciones = false;
            var _IdPluviometro = 0;
            for (let index = 0; index < _Registros.length; index++){
                let _Reg = _Registros[index];
                _IdPluviometro = _Reg.IdPluviometro;
                var _Par = {
                    IdUsuario: _AppVue.Datos._User._ID,
                    IdPluviometro: _Reg.IdPluviometro,
                    Valor: _Reg.Valor,
                    Fecha: _Reg.Fecha
                };
                let _Retorno = await this.IntentoAltaRegistroPluviometro(_Par, false, _AppVue);
                if(_Retorno){
                    _HuboActualizaciones = true;
                    _ArchivosSincronizados += 1;
                }else{
                    _RegistrosAux.push(_Reg);
                }
            }
            if(_HuboActualizaciones){
              Clases.GuardarJsonEnStorage(this._KeyRegistrosPendientesGeneralPluviometros + _IdPluviometro, _RegistrosAux);
              ActualizarComponentePluviometro(_AppVue, _IdPluviometro);
              console.log("RegistrosPluviometros sincronizados!");
            }
        }

        if(_ArchivosSincronizados > 0){
          _AppVue.Notificacion(_ArchivosSincronizados + " registros de pluviómetro sincronizados!", "", "", 3);
        }
    } catch (error) {
        console.log(error);
    }
}
module.exports.IntentoAltaRegistroPluviometro = async (_Par, _AgregoPendienteSiFalla, _AppVue) => {
    var _Retorno = false;
    try {
        var _This = this;
        await axios
            .post(Clases.UrlPosts() + "/AgroIdeas/AltaRegistroPluviometro", _Par)
            .then(function (response) {
                _Retorno = true;
                //_This.$parent.Notificacion("Registro guardado!", "También ha quedado correctamente sincronizado con el servidor.", "success", 8);
            })
            .catch(function (error) {
                if(_AgregoPendienteSiFalla){
                    var _Key = _This._KeyRegistrosPendientesGeneralPluviometros + _Par.IdPluviometro;
                    var _Pendientes = Clases.RecuperarJsonDeStorage(_Key);
                    if(_Pendientes == null){ _Pendientes = []; }
                    _Pendientes.push(_Par);
                    Clases.GuardarJsonEnStorage(_Key, _Pendientes);
                    //this.ActualizarComponentePluviometro(_AppVue, _Par.IdPluviometro);
                    _AppVue.Notificacion("Registro guardado!", "Se sincronizará automáticamente con el servidor cuando vuelvas a tener conexión.", "warn", 8);
                }
            })
            .then(function () {
            });
    } catch (error) {
        console.log(error);
    }
    return _Retorno;
}
module.exports.ObtenerRegistrosPluviometro = (_Id, _Componente) => {
  var _MD = this;
    try {
      axios
        .post(
            Clases.UrlPosts() + "/AgroIdeas/RegistrosPluviometro",
            {
                IdPluviometro: _Id
            }
        )
        .then(function (response) {
          _MD.GuardarDataEnCachePluviometro(response.data);
          if(_Componente){
            if (_Componente.VistaActual(_Id)){
              _Componente.InfoPluviometro = response.data;
              _Componente.DataActualizada = true;
              _Componente.AuxiliaresVista(_Id);
            }
          }
        })
        .catch(function (error) {
          if(_Componente){
            if (_Componente.VistaActual(_Id)){
              _Componente.DataActualizada = false;
              _Componente.AuxiliaresVista(_Id);
            }
          }
        })
        .then(function () {});
    } catch (error) {
      console.log(error);
    }
}
ActualizarComponentePluviometro = (_AppVue, _IdPluviometro) => {
  try {
      let _Componente = _AppVue.$children.find(child => { return child.$options.name === "Pluviometro"; });
      this.ObtenerRegistrosPluviometro(_IdPluviometro, _Componente);
  } catch (error) {
    console.log(error);
  }
}
//#endregion

