<template>
  <div class="w100 Contenido">
    <ModalNuevoEstablecimiento ref="Modal" v-show="ModalVisible" @Cerrar="CerrarModal" />  

    <div class="TituloSeccion">
      <font-awesome-icon  icon="users" size="4x" :style="{ color: 'black'}"  class="IconoTituloSeccion fLeft"/>
      <div class="fLeft"><b>{{RetornoAPI._NombreProductor}}</b></div>

      <!-- <div class="fRight mR5 BotonAgregar" @click="NuevoEstablecimiento()">
        +
      </div> -->
      <div class="clear"></div>
    </div>

    <div class="DivInfoData" ref="DivInfoData">
      Info recuperada de fecha: <span ref="FechaData"></span>
      
      <div class="fRight" v-on:click.stop="ObtenerDatos()">
        <font-awesome-icon  icon="sync-alt" size="4x" :style="{ color: 'white'}"  class="IconoActualizar fLeft"/>
        <!-- Actualizar -->
        <div class="clear"></div>
      </div>
      
      <div class="clear"></div>
    </div>

    <table>
      <thead>
        <tr>
          <th>Establecimiento</th>
          <th class="pR">
            <font-awesome-icon 
            v-on:click.stop="NuevoEstablecimiento()" 
            icon="plus-circle" size="4x" :style="{ color: '#005625'}" class="IconoAgregar Centro"/>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="Est in RetornoAPI._Establecimientos" :key="Est._Id">
          <td v-on:click.stop="ClickMenu(Est._Id, $event)">{{ Est._Nombre }}, {{ Est._Localidad }}</td>
          <td class="c3" v-show="AdminLogueado">
            <font-awesome-icon v-on:click.stop="EditarEstablecimiento(Est)"  icon="edit" size="4x" :style="{ color: '#348E54'}"  class="h100 IconoEliminar cP"/>
            <font-awesome-icon v-on:click.stop="EliminarEstablecimiento(Est)"  icon="trash-alt" size="4x" :style="{ color: '#934141'}"  class="h100 IconoEliminar cP"/>
            <div class="MenuOpciones">
              <font-awesome-icon v-on:click.stop="ClickMenu(Est._Id, $event)"  icon="ellipsis-h" size="4x" :style="{ color: '#000'}"  class="h100 IconoEliminar cP"/>
              <div :id="'CM' + Est._Id" class="ContenidoMenu" v-clickoutside="OcultarMenu" :class="{ Visible: (IdMenuVisible==Est._Id)}">
                <div class="SeccionArrastre"></div>
                <!-- Pluviómetro  -->
                <div class="OpcionMenu" v-show="Est._Pluviometro._ID > 0" v-on:click.stop="VerPluviometro(Est._Pluviometro._ID)">
                  <font-awesome-icon icon="eye" size="1x" :style="{ color: '#22878E'}"  class="IconoFontChico"/>
                  Ver Pluviómetro</div>
                <div class="OpcionMenu" v-show="Est._Pluviometro._ID <= 0" v-on:click.stop="AltaPluviometro(Est._Id)">
                  <font-awesome-icon icon="plus-circle" size="1x" :style="{ color: '#359122'}"  class="IconoFontChico"/>
                  Agregar Pluviómetro</div>
                <!-- Freatímetro  -->
                <div class="OpcionMenu" v-show="Est._Freatimetro._ID > 0" v-on:click.stop="VerFreatimetro(Est._Freatimetro._ID)">
                  <font-awesome-icon icon="eye" size="1x" :style="{ color: '#22878E'}"  class="IconoFontChico"/>
                  Ver Freatímetro
                </div>
                <div class="OpcionMenu" v-show="Est._Freatimetro._ID <= 0" v-on:click.stop="AltaFreatimetro(Est._Id)">
                  <font-awesome-icon icon="plus-circle" size="1x" :style="{ color: '#359122'}"  class="IconoFontChico"/>
                  Agregar Freatímetro
                </div>
                <!-- Veris  -->
                <div class="OpcionMenu" v-show="Est._Veris._ID > 0" v-on:click.stop="VerVeris(Est._Veris._ID)">
                  <font-awesome-icon icon="eye" size="1x" :style="{ color: '#22878E'}"  class="IconoFontChico"/>
                  Ver Ambientación
                </div>
                <div class="OpcionMenu" v-show="Est._Veris._ID <= 0" v-on:click.stop="AltaVeris(Est._Id)">
                  <font-awesome-icon icon="plus-circle" size="1x" :style="{ color: '#359122'}"  class="IconoFontChico"/>
                  Agregar Ambientación
                </div>
                <!-- Análisis Agua  -->
                <div class="OpcionMenu" v-show="Est._AnalisisAgua._ID > 0" v-on:click.stop="VerAguas(Est._AnalisisAgua._ID)">
                  <font-awesome-icon icon="eye" size="1x" :style="{ color: '#22878E'}"  class="IconoFontChico"/>
                  Ver Análisis Agua Útil
                </div>
                <div class="OpcionMenu" v-show="Est._AnalisisAgua._ID <= 0" v-on:click.stop="AltaAguas(Est._Id)">
                  <font-awesome-icon icon="plus-circle" size="1x" :style="{ color: '#359122'}"  class="IconoFontChico"/>
                  Agregar Análisis Agua Útil
                </div>
                <!-- Análisis Suelos  -->
                <div class="OpcionMenu" v-show="Est._AnalisisSuelo._ID > 0" v-on:click.stop="VerSuelo(Est._AnalisisSuelo._ID)">
                  <font-awesome-icon icon="eye" size="1x" :style="{ color: '#22878E'}"  class="IconoFontChico"/>
                  Ver Análisis Suelo
                </div>
                <div class="OpcionMenu" v-show="Est._AnalisisSuelo._ID <= 0" v-on:click.stop="AltaSuelo(Est._Id)">
                  <font-awesome-icon icon="plus-circle" size="1x" :style="{ color: '#359122'}"  class="IconoFontChico"/>
                  Agregar Análisis Suelo
                </div>

                <div class="DivSeparador" v-show="(Est._Pluviometro._ID > 0) || (Est._Freatimetro._ID > 0)"></div>

                <div class="OpcionMenu" v-show="Est._Pluviometro._ID > 0" v-on:click.stop="ConfirmarBajaPluviometro(Est._Pluviometro._ID, Est._Nombre + ', ' + Est._Localidad)">
                  <font-awesome-icon icon="trash-alt" size="1x" :style="{ color: '#934141'}"  class="IconoFontChico"/>
                  Eliminar Pluviómetro
                </div>
                <div class="OpcionMenu" v-show="Est._Freatimetro._ID > 0" v-on:click.stop="ConfirmarBajaFreatimetro(Est._Freatimetro._ID, Est._Nombre + ', ' + Est._Localidad)">
                  <font-awesome-icon icon="trash-alt" size="1x" :style="{ color: '#934141'}"  class="IconoFontChico"/>
                  Eliminar Freatímetro
                </div>
                <div class="OpcionMenu" v-show="Est._Veris._ID > 0" v-on:click.stop="ConfirmarBajaVeris(Est._Veris._ID, Est._Nombre + ', ' + Est._Localidad)">
                  <font-awesome-icon icon="trash-alt" size="1x" :style="{ color: '#934141'}"  class="IconoFontChico"/>
                  Eliminar Ambientación
                </div>
                <div class="OpcionMenu" v-show="Est._AnalisisAgua._ID > 0" v-on:click.stop="ConfirmarBajaAguas(Est._AnalisisAgua._ID, Est._Nombre + ', ' + Est._Localidad)">
                  <font-awesome-icon icon="trash-alt" size="1x" :style="{ color: '#934141'}"  class="IconoFontChico"/>
                  Eliminar Análisis Agua Útil
                </div>
