</template>

<script>
import axios from "axios";
import Clases from "../../Auxiliares/Clases.js";
import ModalNuevoEstablecimiento from '../../components/Utilidades/ModalNuevoEstablecimiento';
import SlidingPagination from 'vue-sliding-pagination';

export default {
  name: "Establecimientos",
  directives: {
    "clickoutside": {
      bind: function(el, binding) {
        // Define ourClickEventHandler
        const ourClickEventHandler = event => {
          if (!el.contains(event.target) && el !== event.target) {
            // as we are attaching an click event listern to the document (below)
            // ensure the events target is outside the element or a child of it
            binding.value(event); // before binding it
          }
        };
        // attached the handler to the element so we can remove it later easily
        el.__vueClickEventHandler__ = ourClickEventHandler;

        // attaching ourClickEventHandler to a listener on the document here
        document.addEventListener("click", ourClickEventHandler);
      },
      unbind: function(el) {
        // Remove Event Listener
        document.removeEventListener("click", el.__vueClickEventHandler__);
      }
    }
  },
  data() {
    return {
      RetornoAPI: {
        _NombreProductor: ""
      },
      ModalVisible: false,
      IdMenuVisible: 0,
      _UserOk: false,
      Error: null,
      AdminLogueado: false,
    };
  },
  mounted() {
    this._UserOk = this.$parent.Datos.ChequearSesionUsuario(this.$router);
    if (this._UserOk){
      this.AdminLogueado = this.$parent.Datos.AdminLogueado();
      if(this.AdminLogueado){
        this.ObtenerDatos();
      }
    }
  },
  methods: {
    ObtenerDatos() {
      var _This = this;
      try {
        axios
          .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/Establecimientos", {
            IdUsuario: _This.$parent.Datos._User._ID,
            IdProductor: _This.$route.params.Id
          })
          .then(function (response) {
            _This.RetornoAPI = response.data;
            _This.GuardarDataEnCache(_This.RetornoAPI);
            _This.$refs.DivInfoData.classList.remove("Visible");
          })
          .catch(function (error) {
            _This.$refs.DivInfoData.classList.add("Visible");
          })
          .then(function () {});
      } catch (error) {
        console.log(error);
      }
    },
    IntentoObtenerDataDeCache(){
      var _Data = Clases.RecuperarJsonDeStorage(this.KeyDataCacheada());
      if (_Data != null){
        this.RetornoAPI = _Data._Json;
        this.$refs.FechaData.innerHTML = _Data._Fecha._FechaYHora;
      }
    },
    GuardarDataEnCache(_InfoAGuardar){
      var _Data = {
        _Fecha: new Clases.Fecha(new Date()),
        _Key: this.KeyDataCacheada(),
        _Json: _InfoAGuardar
      };
      Clases.GuardarJsonEnStorage(this.KeyDataCacheada(), _Data);
    },
    KeyDataCacheada(){
      return "InfoEstablecimientos" + this.$parent.Datos._User._ID;
    },
    VerModal() {
      this.ModalVisible = true;
    },
    CerrarModal() {
      this.ModalVisible = false;
    },
    NuevoEstablecimiento(){
      this.$refs.Modal.SetearPorNuevo(this.RetornoAPI._IdProductor);
      this.VerModal();
    },
    EditarEstablecimiento(_E){
      this.ModalVisible = true;
      this.$refs.Modal.Editar(_E);
    },
    EliminarEstablecimiento(_E){
      var _This = this;
      //alert(_Nombre);
      this.$confirm({
        title: "ATENCIÓN",
        message: "Estás seguro de eliminar el establecimiento " + _E._Nombre + "?",
        button: {
          yes: 'Si',
          no: 'No'
        },
        callback: confirm => {
          if(confirm){
            axios
              .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/BajaEstablecimiento", {
                IdUsuario: _This.$parent.Datos._User._ID,
                IdEstablecimiento: _E._Id
              })
              .then(function (response) {
                if (response.data != ""){
                  _This.$parent.Notificacion(response.data, "", "success", 3);
                  _This.ObtenerDatos();
                }else{
                  _This.$parent.Notificacion("Error1!", "", "error", 3);
                }
              })
              .catch(function (error) {
                _This.$parent.Notificacion("Error!", "", "error", 3);
                console.error(error);
              })
              .then(function () {
                //console.log("Ok");//LLega siempre que no haya catch
              });
          }
        }
      })
    },
    OcultarMenu(){
      this.IdMenuVisible = 0;
    },
    ClickMenu(_Id, event){
      this.IdMenuVisible = _Id;
      let _IdElemento = "CM" + _Id;
      let _Elemento = document.getElementById(_IdElemento);
      let _AltoMenu = event.pageY + 120;
      if (event.pageY + 300 > screen.height){
        _AltoMenu = screen.height - 200;
        //console.log(_AltoMenu);
      } 
      // if (event.pageX > screen.width/2){
      //   _AltoMenu = screen.height - 200;
      // } 
      _Elemento.style.top = _AltoMenu + "px";
      // _Elemento.style.left = _AltoMenu + "px";
      // console.log(event.pageY);
      // console.log(screen.height);
    },
    VerPluviometro(_ID){
      this.$router.push("/pluviometro/" + _ID);
    },
    VerFreatimetro(_ID){
      this.$router.push("/freatimetro/" + _ID);
    },
    AltaPluviometro(_IdEst){
      var _This = this;
      try {
        axios
          .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/AltaPluviometro", {
            IdUsuario: _This.$parent.Datos._User._ID,
            IdEstablecimiento: _IdEst
          })
          .then(function (response) {
            _This.ObtenerDatos();
            // _This.OcultarMenu();
            _This.$parent.Notificacion("Pluviómetro agregado!", "", "success", 3);
          })
          .catch(function (error) {
            console.error(error);
          })
          .then(function () {});
      } catch (error) {
        console.log(error);
      }
    },
    ConfirmarBajaPluviometro(_IdP, _NombreEst){
      this.$confirm({
        title: "ATENCIÓN",
        message: "Estás seguro de eliminar el Pluviómetro de " + _NombreEst + "?",
        button: {
          yes: 'Si',
          no: 'No'
        },
        callback: confirm => {
          if(confirm){ this.BajaPluviometro(_IdP); }
        }
      })
    },
    BajaPluviometro(_Id){
      var _This = this;
      try {
        axios
          .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/BajaPluviometro", {
            IdUsuario: _This.$parent.Datos._User._ID,
            IdPluviometro: _Id
          })
          .then(function (response) {
            _This.ObtenerDatos();
            _This.$parent.Notificacion("Pluviómetro eliminado!", "", "success", 3);
          })
          .catch(function (error) {
            console.log(error.response);
            console.log(error.toString());
            console.log(error.toString().includes("Network error"));
          })
          .then(function () {});
      } catch (error) {
        console.log(error);
      }
    },
    AltaFreatimetro(_IdEst){
      var _This = this;
      try {
        axios
          .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/AltaFreatimetro", {
            IdUsuario: _This.$parent.Datos._User._ID,
