          })
          .catch(function (error) {
            console.error(error);
          })
          .then(function () {});
      } catch (error) {
        console.log(error);
      }
    },
    ConfirmarBajaFreatimetro(_IdF, _NombreEst){
      this.$confirm({
        title: "ATENCIÓN",
        message: "Estás seguro de eliminar el Freatímetro de " + _NombreEst + "?",
        button: {
          yes: 'Si',
          no: 'No'
        },
        callback: confirm => {
          if(confirm){ this.BajaFreatimetro(_IdF); }
        }
      })
    },
    BajaFreatimetro(_Id){
      var _This = this;
      try {
        axios
          .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/BajaFreatimetro", {
            IdUsuario: _This.$parent.Datos._User._ID,
            IdFreatimetro: _Id
          })
          .then(function (response) {
            _This.ObtenerDatos();
            _This.$parent.Notificacion("Pluviómetro eliminado!", "", "success", 3);
          })
          .catch(function (error) {
            console.error(error);
          })
          .then(function () {});
      } catch (error) {
        console.log(error);
      }
    },
    VerVeris(_ID){
      this.$router.push("/veris/" + _ID);
    },
    AltaVeris(_IdEst){
      var _This = this;
      try {
        axios
          .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/AltaVeris", {
            IdUsuario: _This.$parent.Datos._User._ID,
            IdEstablecimiento: _IdEst
          })
          .then(function (response) {
            _This.ObtenerDatos();
            // _This.OcultarMenu();
            _This.$parent.Notificacion("Veris agregado!", "", "success", 3);
          })
          .catch(function (error) {
            console.error(error);
          })
          .then(function () {});
      } catch (error) {
        console.log(error);
      }
    },
    ConfirmarBajaVeris(_Id, _NombreEst){
      this.$confirm({
        title: "ATENCIÓN",
        message: "Estás seguro de eliminar el Veris de " + _NombreEst + "?",
        button: {
          yes: 'Si',
          no: 'No'
        },
        callback: confirm => {
          if(confirm){ this.BajaVeris(_Id); }
        }
      })
    },
    BajaVeris(_Id){
      var _This = this;
      try {
        axios
          .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/BajaVeris", {
            IdUsuario: _This.$parent.Datos._User._ID,
            IdVeris: _Id
          })
          .then(function (response) {
            _This.ObtenerDatos();
            _This.$parent.Notificacion("Veris eliminado!", "", "success", 3);
          })
          .catch(function (error) {
            console.error(error);
          })
          .then(function () {});
      } catch (error) {
        console.log(error);
      }
    },
    VerAguas(_ID){
      this.$router.push("/aguas/" + _ID);
    },
    AltaAguas(_IdEst){
      var _This = this;
      try {
        axios
          .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/AltaAguas", {
            IdUsuario: _This.$parent.Datos._User._ID,
            IdEstablecimiento: _IdEst
          })
          .then(function (response) {
            _This.ObtenerDatos();
            // _This.OcultarMenu();
            _This.$parent.Notificacion("Análisis Agua agregado!", "", "success", 3);
          })
          .catch(function (error) {
            console.error(error);
          })
          .then(function () {});
      } catch (error) {
        console.log(error);
      }
    },
    ConfirmarBajaAguas(_Id, _NombreEst){
      this.$confirm({
        title: "ATENCIÓN",
        message: "Estás seguro de eliminar el Análisis Agua de " + _NombreEst + "?",
        button: {
          yes: 'Si',
          no: 'No'
        },
        callback: confirm => {
          if(confirm){ this.BajaAguas(_Id); }
        }
      })
    },
    BajaAguas(_Id){
      var _This = this;
      try {
        axios
          .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/BajaAguas", {
            IdUsuario: _This.$parent.Datos._User._ID,
            Id: _Id
          })
          .then(function (response) {
            _This.ObtenerDatos();
            _This.$parent.Notificacion("Análisis Agua eliminado!", "", "success", 3);
          })
          .catch(function (error) {
            console.error(error);
          })
          .then(function () {});
      } catch (error) {
        console.log(error);
      }
    },
    VerSuelo(_ID){
      this.$router.push("/suelos/" + _ID);
    },
    AltaSuelo(_IdEst){
      var _This = this;
      try {
        axios
          .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/AltaSuelo", {
            IdUsuario: _This.$parent.Datos._User._ID,
            IdEstablecimiento: _IdEst
          })
          .then(function (response) {
            _This.ObtenerDatos();
            // _This.OcultarMenu();
            _This.$parent.Notificacion("Análisis Suelo agregado!", "", "success", 3);
          })
          .catch(function (error) {
            console.error(error);
          })
          .then(function () {});
      } catch (error) {
        console.log(error);
      }
    },
    ConfirmarBajaSuelo(_Id, _NombreEst){
      this.$confirm({
        title: "ATENCIÓN",
        message: "Estás seguro de eliminar el Análisis Suelo de " + _NombreEst + "?",
        button: {
          yes: 'Si',
          no: 'No'
        },
        callback: confirm => {
          if(confirm){ this.BajaSuelo(_Id); }
        }
      })
    },
    BajaSuelo(_Id){
      var _This = this;
      try {
        axios
          .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/BajaSuelo", {
