    }
  },
  components: {
    ModalNuevoEstablecimiento,
    SlidingPagination
  },
};
</script>

<style lang="scss" scoped>
  .c1{width:40%;} 
  .c3{width:33%;} 
  tr{
    cursor:pointer;
  }

  //#region "MenuOpciones"
  .MenuOpciones{
    width:30px; height:18px; position:relative; display:inline-block;
  }
  .ContenidoMenu{
    position:absolute;
    right: 30px; top: 18px;
    background-color: rgba(255, 255, 255, 0.99);
    min-width:200px;;
    width:200px;
    z-index:100;
    border-radius:5px 5px 0 0;
    box-shadow:0 0 10px #666;
    box-sizing:border-box;
    overflow:hidden;
    height:auto;
    max-height:0px;
    position:fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    
  }
  .ContenidoMenu.Oculto{max-height:0px; 
    padding:0px; 
    border: none;
  }
  .ContenidoMenu.Visible{
    // padding:5px;
    // border:1px solid #444;
    max-height:400px;
  }
  .SeccionArrastre{
    background-color: #555; height:10px;
    width:100%;
  }
  .SpanEliminar{
    border-radius:20px; background-color: #922;
    color:white;
    padding:2px 5px;
    line-height:5px;
    height:5px;
  }
  .IconoFontChico{
    width:15px; height:15px;
  }
  .OpcionMenu{
    width:100%;
    line-height:35px;
    padding:0 10px;
    box-sizing:border-box;
    text-align:left;
    cursor:pointer;
    &:hover{
      background-color: rgba(0,0,0,0.1);
    }
    color:#555;
  }
  .BordeSuperior{
    border-top:1px solid rgba(0,0,0,0.6);
  }
  //#endregion

  .svg-inline--fa.fa-w-18, .svg-inline--fa.fa-w-14, .svg-inline--fa.fa-w-16 {
      width: auto !important;
