<template>
  <div class="w100 Contenido">

    <div class="TituloSeccion">
      <font-awesome-icon  icon="water" size="4x" :style="{ color: 'black'}"  class="IconoTituloSeccion fLeft"/>
      <div class="fLeft">Análisis Agua Útil</div>
      <div class="clear"></div>
    </div>
    <div class="DivInfoData" ref="DivInfoData">
      Info recuperada de fecha: <span ref="FechaData"></span>
      
      <div class="fRight" v-on:click.stop="ObtenerDatos()">
        <font-awesome-icon  icon="sync-alt" size="4x" :style="{ color: 'white'}"  class="IconoActualizar fLeft"/>
        <!-- Actualizar -->
        <div class="clear"></div>
      </div>
      
      <div class="clear"></div>
    </div>
    <table>
      <thead>
        <tr>
          <th>Productor</th>
          <th>Establ.</th>
          <th>Localidad</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="p in Registros" :key="p._Id" v-on:click="VerRegistro(p._Id)">
          <td>{{ p._NombreProductor }}</td>
          <td>{{ p._NombreEstablecimiento }}</td>
          <td>{{ p._Localidad }}</td>
        </tr>
      </tbody>
    </table>

    <br><br>
    <router-link class="BotonVerMapa dIB" to="/VerMapa">Ver Mapa</router-link>
  </div>
</template>

<script>
import axios from "axios";
import Clases from "../Auxiliares/Clases.js";

export default {
  name: "Aguas",
  data() {
    return {
      Registros: null,
      _UserOk: false,
      Error: null,
    };
  },
  mounted() {
    this._UserOk = this.$parent.Datos.ChequearSesionUsuario(this.$router);
    if (this._UserOk){
      this.IntentoObtenerDataDeCache();
      this.ObtenerDatos();
    }
  },
  methods: {
    ObtenerDatos() {
      var _This = this;
      try {
        axios
          .post(_This.$parent.Datos.UrlPosts() + "/AgroIdeas/Aguas", {
            IdUsuario: _This.$parent.Datos._User._ID,
          })
          .then(function (response) {
            _This.Registros = response.data;
            _This.GuardarDataEnCache(_This.Registros);
            _This.$refs.DivInfoData.classList.remove("Visible");
          })
          .catch(function (error) {
              _This.$refs.DivInfoData.classList.add("Visible");
              //_This.IntentoObtenerDataDeCache();
          })
          .then(function () {
          });
      } catch (error) {
        console.log(error);
      }
    },
    VerRegistro(id) {
      this.$router.push("/aguas/" + id);
    },
    IntentoObtenerDataDeCache(){
      var _Data = Clases.RecuperarJsonDeStorage(this.KeyDataCacheada());
      if (_Data != null){
        this.Registros = _Data._Json;
        this.$refs.FechaData.innerHTML = _Data._Fecha._FechaYHora;
      }
    },
    GuardarDataEnCache(_InfoAGuardar){
      var _Data = {
        _Fecha: new Clases.Fecha(new Date()),
        _Key: this.KeyDataCacheada(),
        _Json: _InfoAGuardar
      };
      Clases.GuardarJsonEnStorage(this.KeyDataCacheada(), _Data);
    },
    KeyDataCacheada(){
      return "InfoAguas" + this.$parent.Datos._User._ID;
    }
  },
  components: {
  },
};
</script>

<style lang="scss" scoped>
@import "../Css/Componentes.scss";
@import "../Css/Globales.css";

.button {
  background-color: #4caf50; /* Green */
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  transition-duration: 0.4s;
  cursor: pointer;
  margin: 30px 0;
}
.Verde {
  background-color: white;
  color: black;
  border: 2px solid #4caf50;
}

.Verde:hover {
  background-color: #4caf50;
  color: white;
}

.Azul {
  background-color: white;
  color: black;
  border: 2px solid #008cba;
}

.Azul:hover {
  background-color: #008cba;
  color: white;
}

.Gris {
  background-color: white;
  color: black;
  border: 2px solid #e7e7e7;
}

.Gris:hover {
  background-color: #e7e7e7;
}

.Negro {
  background-color: white;
  color: black;
  border: 2px solid #555555;
}

.Negro:hover {
  background-color: #555555;
  color: white;
}

</style>
